<template>
    <view class="">
        <!-- 底部输入栏 -->
        <view class="input-box" :class="popupLayerClass" @touchmove.stop.prevent="discard">
            <view class="textbox">
                <view class="text-mode" :class="isVoice?'hidden':''">
                    <view class="box">
                        <textarea auto-height="true" v-model="textMsg" @focus="textareaFocus" />
                    </view>
        		</view>
        	</view>
        	<!-- #ifndef H5 -->
        	<view class="more" @tap="showMore">
        		<view class="icon add"></view>
        	</view>
        	<!-- #endif -->
        	<view class="send" :class="isVoice?'hidden':''" @tap="send('text')">
        		<view class="btn">发送</view>
        	</view>
        </view>
        <view class="popup-layer" :class="popupLayerClass" @touchmove.stop.prevent="discard">
        	<!-- <emotion @addEmoji="addEmoji" :class="{hidden:hideEmoji}" ></emotion> -->
        	<view class="more-layer" :class="{hidden:hideMore}">
        		<view class="list">
        			<view class="box" @tap="chooseImage">
        				<view class="icon tupian2"></view>
        			</view>
        		<!-- 	<view class="box" @tap="yuyintonghua">
        				<image style="font-size:16px;width: 32px; height: 32px;" 
        				src="../../static/yuyintonghua.png"></image>
        			</view> -->
        			<view class="box" @tap="chooseFile">
        				<view class="icon icon-wenjian">
        				    
        				</view>
        			</view>
        
        		</view>
        	</view>
        </view>
    </view>
</template>

<script>
    import {sendMsg} from '../../../network/msg.js'
    import {upLoadFile} from '../../../network/file.js'
    export default{
        data() {
            return {
                // 抽屉参数
                popupLayerClass: '',
                isVoice: false,
                recording: false,
                //文字消息
                textMsg: '',
                voiceTis: '按住 说话',
                // more参数
                hideMore: true,
                
                // //录音相关参数
                // // #ifndef H5
                // //H5不能录音
                // RECORDER: uni.getRecorderManager(),
                // // #endif
                // VoiceTimer: null,
                // recordTis: "手指上滑 取消发送",
                // willStop: false,
                // initPoint: {
                //     identifier: 0,
                //     Y: 0,
                //     timestamp:0
                // },
                // recordTimer: null,
                // recordLength: 0,
            }
        },
        computed: {
        
        },
        created() {

        },
        methods:{
			
            send(type,option){
                if(type=='text'){
					
					let params = {
						contact_id: getApp().globalData.currentTo_user_info.contact_id,
						content:this.textMsg,
						content_type: 0,
						token:getApp().globalData.user_token
					}
					sendMsg(params).then(res => {
						console.log(res)
						if(res.data.code = 1){
							console.log(res)
							this.$emit("submitMsg",1);
						}
					})
                    
                    this.textMsg=""
                }           
            },
            chooseImage(){
            	// this.getImage('album');
            },
            chooseFile(){
                // // #ifdef  MP-WEIXIN
                // wx.chooseMessageFile({
                //   count: 1,
                //   type: 'file',
                //   success (res) {
                //     // tempFilePath可以作为img标签的src属性显示图片
                //     const tempFilePaths = res.tempFiles
                //     console.log(tempFilePaths,'tempFilePaths');
                //   }
                // })
                // // #endif
            },
            //选照片 or 拍照
            getImage(type){
            	// this.hideDrawer();
            	// uni.chooseImage({
             //        count:1,
            	// 	sourceType:[type],
            	// 	sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
            	// 	success: (chooseImageRes)=>{
             //            const tempFilePaths = chooseImageRes.tempFilePaths;
             //            console.log(upLoadFile);
             //            upLoadFile({
             //                filePath:tempFilePaths[0]
             //            }).then(res=>{
             //                this.send('image',{url:res.url})
             //            }).catch(err=>{
             //                console.log(err);
             //            })
            	// 	}
            	// });
            },
            showMore() {
                this.isVoice = false;
                this.hideEmoji = true;
                if (this.hideMore) {
                    this.hideMore = false;
                    this.openDrawer();
                } else {
                    this.hideDrawer();
                }
            },
            // 打开抽屉
            openDrawer() {
                this.popupLayerClass = 'showLayer';
            },
            // 隐藏抽屉
            hideDrawer() {
                this.popupLayerClass = '';
                setTimeout(() => {
                    this.hideMore = true;
                    this.hideEmoji = true;
                }, 150);
            },
            textareaFocus() {
                if (this.popupLayerClass == 'showLayer' && this.hideMore == false) {
                    this.hideDrawer();
                }
            },
            discard() {
            	return;
            },
        },
        
    }
</script>

<style lang="scss" scoped>
@font-face {
    font-family:"HMfont-home";
    src:url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');}
	.icon {
		font-family:"HMfont-home" !important;
		font-size:56upx;
		font-style:normal;
		color: #333;
		&.biaoqing:before {
		  content: "\e797";
		}
		&.jianpan:before {
		  content: "\e7b2";
		}
		&.yuyin:before {
		  content: "\e805";
		}
		&.tupian:before {
		  content: "\e639";
		}
		&.chehui:before {
		  content: "\e904";
		}
		&.luyin:before {
		  content: "\e905";
		}
		&.luyin2:before {
		  content: "\e677";
		}
		&.other-voice:before {
		  content: "\e667";
		}
		&.my-voice:before {
		  content: "\e906";
		}
		&.hongbao:before {
		  content: "\e626";
		}
		&.tupian2:before {
		  content: "\e674";
		}
		&.paizhao:before {
		  content: "\e63e";
		}
		&.add:before {
		  content: "\e655";
		}
		&.close:before {
		  content: "\e607";
		}
		&.to:before {
		  content: "\e675";
		}
	}
.hidden{
	display: none !important;
}
.popup-layer{
	&.showLayer{transform: translate3d(0,-42vw,0);}
	transition: all .15s linear;
	width: 96%;
	height: 42vw;
	padding: 20upx 2%;
	background-color: #f3f3f3;
	border-top: solid 1upx #ddd;
	position: fixed;
	z-index: 20;
	top: 100%;
	.emoji-swiper{
		height: 40vw;
		swiper-item{
			display: flex;
			align-content: flex-start;
			flex-wrap: wrap;
			view{
				width: 12vw;
				height: 12vw;
				display: flex;
				justify-content: center;
				align-items: center;
				image{
					width: 8.4vw;
					height: 8.4vw;
				}
			}
		}
	}
	.more-layer{
		width: 100%;
		height: 42vw;
		.list{
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			.box{
				width: 18vw;
				height: 18vw;
				border-radius: 20upx;
				background-color: #fff;
				display: flex;
				justify-content: center;
				align-items: center;
				margin: 0 3vw 2vw 3vw;
				.icon{
					font-size: 70upx;
				}
			}
		}
	}
}
.input-box{
	width: 98%;
	min-height: 100upx;
	padding: 0 1%;
	background-color: #FFFFFF;
	display: flex;
	position: fixed;
	z-index: 20;
	bottom:-2upx;
	&.showLayer{transform: translate3d(0,-42vw,0);}
	transition: all .15s linear;
	border-bottom: solid 1upx #ddd;
	.voice,.more{
		flex-shrink: 0;
		width: 90upx;
		height: 100upx;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	
	.send{
		//H5发送按钮左边距
		/* #ifdef H5 */
		margin-left: 20upx;
		/* #endif */
		flex-shrink: 0;
		width: 100upx;
		height: 100upx;
		display: flex;
		align-items: center;
		.btn{
			width: 90upx;
			height: 56upx;
			display: flex;
			justify-content: center;
			align-items: center;
			background:linear-gradient(to right, #36b2fe, #3061fb);
			color: #fff;
			border-radius: 6upx;
			font-size: 24upx;
		}
	}
	.textbox{
		width: 100%;
		min-height: 70upx;
		margin-top: 15upx;
		.voice-mode{
			width: calc(100% - 2upx);
			height: 68upx;
			border-radius: 70upx;
			border:solid 1upx #cdcdcd;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 28upx;
			background-color: #fff;
			color: #555;
			&.recording{
				background-color: #e5e5e5;
			}
		}
		.text-mode{
			width: 100%;
			min-height: 70upx;
			display: flex;
			background-color: #fff;
			border-radius: 40upx;
			.box{
				width: 100%;
				padding-left: 30upx;
				min-height: 70upx;
				display: flex;
				align-items: center;
				textarea{
					width: 100%;
				}
			}
			.em{
				flex-shrink: 0;
				width: 80upx;
				padding-left: 10upx;
				height: 70upx;
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
	}
}
.record{
	width: 40vw;
	height: 40vw;
	position: fixed;
	top: 55%;
	left: 30%;
	background-color: rgba(0,0,0,.6);
	border-radius: 20upx;
	.ing{
		width: 100%;
		height: 30vw;
		display: flex;
		justify-content: center;
		align-items: center;
		// 模拟录音音效动画
		@keyframes volatility {
			0% {background-position: 0% 130%;}
			20% {background-position: 0% 150%;}
			30% {background-position: 0% 155%;}
			40% {background-position: 0% 150%;}
			50% {background-position: 0% 145%;}
			70% {background-position: 0% 150%;}
			80% {background-position: 0% 155%;}
			90% {background-position: 0% 140%;}
			100% {background-position: 0% 135%;}
		}
		.icon{
			background-image:linear-gradient(to bottom,#f09b37,#fff 50%); 
			background-size:100% 200%;
			animation: volatility 1.5s ease-in-out -1.5s infinite alternate;
			-webkit-background-clip:text; 
			-webkit-text-fill-color:transparent; 
			font-size: 150upx;
			color: #f09b37;
		}
	}
	.cancel{
		width: 100%;
		height: 30vw;
		display: flex;
		justify-content: center;
		align-items: center;
		.icon{
			color: #fff;
			font-size: 150upx;
		}
	}
	.tis{
		width: 100%;
		height: 10vw;
		display: flex;
		justify-content: center;
		font-size: 28upx;
		color: #fff;
		&.change{
			color: #f09b37;
		}
	}
}
</style>
